.background-container {
  background-color: #a2a6a7;
  min-height: 100vh; 
  display: flex;
  flex-direction: column;
}
.logo {
    color: rgb(142, 62, 62);
    font-size: 40px;
    margin-left: 20px;
  }
  
  
  .pages {
    background-color: #d8a027;
    padding: 15px ;
    display: flex;
    justify-content: flex-end; 
   
  }
  
 
 
  .right-links-container {
    display: flex;
  list-style-type: none;
  margin: 0;
  padding: 10px;
  
  
  }
  .right-links-container li {
    margin-left: 20px;
  }
  
  .right-links-container li:first-child {
    margin-left: 0;
  }
  
  .right-links-container li a {
    color: white;
    text-decoration: none;
    text-decoration: none;
    padding: 8px 12px;
    border-radius: 20px;
    background-color: #c4658b;
    
  }

  .right-links-container li a:hover {
    background-color: #a23a63;
  }
  
  .container {
   
    
    padding: 0 20px;
    
  }
  
  .login-container {
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    padding: 30px;
    margin-top: 50px;
    margin-bottom: 50px;
    border: 3px solid #431e1e;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-sizing: border-box;
  }
  
  .login-container h2 {
    margin-bottom: 20px;
  }
  
  .login-container label {
    display: block;
    margin-bottom: 20px;
  }
  
  .login-container input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 2px solid #ccc;
    border-radius: 3px;
  }
  
  .login-container button {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    background-color: #fff200;
    color: #2ec336;
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }
  
  .login-container button:hover {
    background-color: #ed3c18;
  }
  
  .login-container .error-message {
    color: red;
    margin-top: 10px;
  }
  
  .app {
    background-color: #e317c1;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }